import { usePathname, useRouter } from "next/navigation";
import { FaHome, FaHistory, FaCalendarAlt, FaUser } from "react-icons/fa";

export default function BottomNav() {
  const router = useRouter();
  const pathname = usePathname();

  const navItems = [
    { path: "/dashboard", icon: FaHome, label: "Home" },
    { path: "/dashboard/history", icon: FaHistory, label: "History" },
    { path: "/dashboard/appointments", icon: FaCalendarAlt, label: "Appointments" },
    { path: "/dashboard/profile", icon: FaUser, label: "Profile" },
  ];

  return (
    <div className="fixed bottom-0 inset-x-0 bg-gradient-to-r from-blue-400 to-purple-500 shadow-inner p-1 flex justify-around items-center rounded-t-lg">
      {navItems.map(({ path, icon: Icon, label }) => {
        const isActive = pathname === path;
        return (
          <button
            key={path}
            onClick={() => router.push(path)}
            className="flex flex-col items-center text-[10px]"
          >
            <Icon size={18} className={isActive ? "text-purple-700" : "text-white"} />
            {isActive && <span className="text-purple-700 mt-0.5">{label}</span>}
          </button>
        );
      })}
    </div>
  );
}
